body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  background-color: black;
  height: 100px;
}

.navLabel {
  text-align: center;
  color: #ffffff;
  margin: 25px 0 25px 0px;
  font-size: 50px;
}
.imgCont {
  width: 80%;
  margin: 30px 20% 0  20%;
  align-content: center;
}

.compImg {
  height: 200px;
  width: 200px;
  float: left;
  margin: 10px;
}

.jumbotron {
  text-align: center;
  position: absolute;
  top: 30px;
  width:100%;

}
.scorecard {
  color: white;
}
